*{
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  padding: 0;
}

body{
  padding: 60px;
}

input{
  width: 100%;
  font-size: 1.3rem;
  color: #c5c5c5;
}

.card{
  align-content: center;
  border: 1px solid grey;
  border-radius: 6px;
  margin: 0 10px 10px 0;
  overflow: hidden;
}

.card p{
  padding: 5px;
  text-align: center;
}

.card img{
  max-height: 170px;;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}